<template>
  <div>
    <b-modal id="modal-center" centered title="Send Message">
      <form id="app" action="#" method="post">
        <div>
          <label for="name"></label>
          <input
            id="name"
            required
            class="form-control"
            placeholder="Please Enter Your Name (Required)"
            v-model="name"
            type="text"
            name="name"
          />
        </div>

        <div>
          <input
            id="email"
            required
            class="form-control"
            placeholder="Please Enter Your Email (Required)"
            v-model="email"
            type="email"
            name="email"
            min="0"
          />
        </div>

        <div>
          <input
            id="phone"
            required
            class="form-control"
            placeholder="Please Enter Your Number (Required)"
            v-model="phone"
            type="tel"
            name="phone"
            min="0"
          />
        </div>

        <div>
          <textarea
            id="msg"
            class="form-control"
            placeholder="Message"
            v-model="msg"
            type="text"
            name="msg"
            min="0"
          />
        </div>

        <div>
          <input type="submit" class="btn btn-success" value="Submit" />
        </div>
      </form>
    </b-modal>
  </div>
</template>

<script>
export default {
  name: "Form",
  data: function() {
    return {
      errors: [],
      name: null,
      email: null,
      phone: null,
      msg: null
    };
  }
};
</script>
