import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";
import "./scss/main.scss";
import BootstrapVue from "bootstrap-vue";
import "bootstrap/dist/css/bootstrap.css";
import "bootstrap-vue/dist/bootstrap-vue.css";
Vue.use(BootstrapVue);

import VueCarousel from "vue-carousel";
Vue.use(VueCarousel);
import { Carousel, Slide } from "vue-carousel";

import VuePageTransition from "vue-page-transition";
Vue.use(VuePageTransition);

import { library } from "@fortawesome/fontawesome-svg-core";
import brands, { faFacebook } from "@fortawesome/fontawesome-free-brands";
import {
  faLink,
  faPhone,
  faQuestion,
  faPlus,
  faEnvelope,
  faTimes
} from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/vue-fontawesome";
library.add(
  faLink,
  faPhone,
  faQuestion,
  faFacebook,
  faPlus,
  faEnvelope,
  faTimes
);

Vue.component("font-awesome-icon", FontAwesomeIcon);

Vue.config.productionTip = false;

const prod = process.env.NODE_ENV === "production";
const shouldSWDev = "serviceWorker" in navigator && !prod;
const shouldSW = "serviceWorker" in navigator && prod;
if (shouldSW) {
  navigator.serviceWorker.register("dist/service-worker.js").then(() => {
    console.log("Service Worker Registered!");
  });
} else if (shouldSWDev) {
  navigator.serviceWorker.register("/service-workder-dev.js").then(() => {
    console.log("Service Worker Registered!");
  });
}

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount("#app");

export default {
  components: {
    Carousel,
    Slide
  }
};
